/* Btn */

.btn {
	display: inline-block;
	height: 48px;
	padding: 12px 28px;

	border-radius: 5px;
	background-color: var(--accent);
	color: var(--white);
	letter-spacing: 0.15px;
	font-size: 16px;
	font-weight: 500;

	transition: opacity 0.2s ease-in;
}

.btn:hover {
	opacity: 0.8;
}

.btn:active {
	position: relative;
	top: 1px;
}

/* Btn outline */

.btn-outline {
	display: flex;
	column-gap: 10px;
	align-items: center;

	height: 48px;
	padding: 12px 20px;

	border-radius: 5px;
	border: 1px solid #000;
	background-color: #fff;
	color: #000;

	transition: opacity 0.2s ease-in;
}

.btn-outline:hover {
	opacity: 0.8;
}

.btn-outline:active {
	position: relative;
	top: 1px;
}

/* Projects  */

.projects {
	/*  display: flex; */
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: center;
	justify-items: center;
	/* flex-wrap: wrap; */
	column-gap: 30px;
	row-gap: 30px;
}

.view {
	width: 300px;
	height: 200px;
	margin: 10px;
	float: left;
	border: 10px solid #fff;
	overflow: hidden;
	position: relative;
	text-align: center;
	box-shadow: 1px 1px 2px #e6e6e6;
	cursor: default;
	background: #fff no-repeat center center
	/* background: #fff url(../images/bgimg.jpg) no-repeat center center */
 }
 .view .mask, .view .content {
	width: 300px;
	height: 200px;
	position: absolute;
	overflow: hidden;
	top: 0;
	left: 0
 }
 .view img {
	display: block;
	position: relative
 }
 .view h2 {
	text-transform: uppercase;
	color: #fff;
	text-align: center;
	position: relative;
	font-size: 17px;
	padding: 10px;
	background: rgba(0, 0, 0, 0.8);
	margin: 20px 0 0 0
 }
 .view p {
	font-family: Georgia, serif;
	font-style: italic;
	font-size: 12px;
	position: relative;
	color: #fff;
	padding: 10px 20px 20px;
	text-align: center;
 }
 .view a.info {
	display: inline-block;
	text-decoration: none;
	padding: 7px 14px;
	background: #000;
	color: #fff;
	text-transform: uppercase;
	box-shadow: 0 0 1px #000
 }
 .view a.info:hover {
	box-shadow: 0 0 5px #000
 } 

 .view img {
	transition: all 0.4s ease-in-out 0.2s;
	opacity: 1;
 }
 .view .mask {
	background-color: rgba(0,0,0,0.8);
	opacity: 0;
	transform: scale(0) rotate(-180deg);
	transition: all 0.4s ease-in;
	border-radius: 0px;
 }
 .view h2{
	opacity: 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.3);
	background: transparent;
	margin: 20px 40px 0px 40px;
	transition: all 0.5s ease-in-out;
 }
 .view p {
	opacity: 0;
	transition: all 0.5s ease-in-out;
 }
 .view a.info {
	opacity: 0;
	transition: all 0.5s ease-in-out;
 }

 .view:hover .mask {
	opacity: 1;
	transform: scale(1) rotate(0deg);
	transition-delay: 0.2s;
 }
 .view:hover img       {
	transform: scale(0);
	opacity: 0;
	transition-delay: 0s;
 }
 .view:hover h2,
 .view:hover p,
 .view:hover a.info{
	opacity: 1;
	transition-delay: 0.5s;
 }



/* Project details */

.project-details {
	margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}

.project-details__cover {
	max-width: 100%;
	margin-bottom: 40px;

	box-shadow: var(--box-shadow);
	border-radius: 10px;
}

.project-details__desc {
	margin-bottom: 30px;

	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}
.project-details__stack {
	margin-bottom: 30px;

	/* font-weight: 700; */
	font-size: 16px;
	line-height: 1.3;
}
.project-details__responsibilities {
	list-style-type: square;
	text-align: left;
	margin-bottom: 30px;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* background: #fff; */

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}





/* Content list */

.content-list {
	margin: 0 auto;
	max-width: 570px;
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 40px;
	text-align: center;
}

.content-list a {
	color: var(--accent);
}

.content-list__item {
	font-size: 18px;
	line-height: 1.5;
}
.content-list__item .title-2{
	margin-top: 30px;
}

.content-list__item p + p {
	margin-top: 0.5em;
}

.link-container {
	display: flex;
	gap: 20px;
}

.work_exp p{
	text-align: left;
}
.work_exp .description,
.work_exp .responsibilities,
.work_exp .tools {
	font-size: 1.0rem;
}



/* Footer */



@media (max-width: 620px) {
	/* Header  */

	.header {
		min-height: unset;
	}

	.header__title {
		font-size: 30px;
	}

	.header__title strong {
		font-size: 40px;
	}

	/* Nav */

	.nav-row {
		justify-content: space-between;
	}

	.dark-mode-btn {
		order: 0;
	}

	/* General */

	.section {
		padding: 40px 0;
	}

	.title-1 {
		margin-bottom: 30px;
		font-size: 40px;
	}

	.title-2 {
		margin-bottom: 10px;
		font-size: 30px;
	}

	.container_aboutme .title-2 {
		margin-bottom: 30px;
		/* font-size: 30px; */
	}

	/* Projects */

	.project__title {
		font-size: 22px;
	}

	

	/* Project page */

	.project-details__desc {
		margin-bottom: 20px;
		font-size: 22px;
	}

	

	/* Content List */

	.content-list {
		row-gap: 20px;
	}
	

	.content-list__item {
		font-size: 16px;
	}
	


	/* Footer */

	.footer {
		padding: 40px 0 30px;
	}

	.footer__wrapper {
		row-gap: 20px;
	}

	/* Social */

	.social {
		column-gap: 20px;
	}

	.social__item {
		width: 28px;
	}
}
